$mainFont: 'Open Sans Condensed', sans-serif;
$secondFont: 'Open Sans', sans-serif;
$fontSize: 14px;
$color:  #D1A954;
$colorDark: #333;
$colorLight: #fff;


* {
	box-sizing: border-box;
}
html {
	overflow-x: hidden;
	scroll-behavior: smooth;
}
body {
	font-family: $mainFont;
	font-size: $fontSize;
	color: #fff;
	background: $colorDark;
}

p, input, textarea {
	font-family: $secondFont;
}

//fast transition
button, .nav {
	transition: 0.2s all linear;
}

//hover
.nav li, .social__icon, .social__arrow, .nav__collapse-social a {
	transition: 0.2s all linear;
	&:hover {
		color: $color;
	}
}

a, a:hover {
	text-decoration: none;
	color: inherit;
}

button {
	background: $color;
	text-transform: uppercase;
	font-size: 18px;
	font-weight: 700;
	height: 50px;
	border: none;
	color: $colorLight;
	&:hover,
	&:focus {
		outline: none;
	}
	&:hover {
		background: $colorLight;
		color: $colorDark;
	}
}
input, textarea {
	color: $colorLight;
	transition: 0.2s all;
	&:hover,
	&:focus {
		outline: none;
	}
}

section {
	margin-top: 150px;
}

h1 {
	font-size: 42px;
	font-weight: 700;
	text-transform: uppercase;
}

h1, p {
	margin-bottom: 50px;
}

h2 {
	font-size: 48px;
	font-weight: 300;
	text-align: center;
	text-transform: uppercase;
	padding-bottom: 100px;
	span {
		color: $color;
	}
}

h3 {
	font-size: 21px;
	font-weight: 700;
	text-transform: uppercase;
	padding-bottom: 40px;
}

.header {
	height: 80vh;
	.container, .row {
		height: 100%;
	}
	.row {
		align-items: center;
	}
	&__button {
		width: 160px;
	}
	&__slider-btn-wrap {
		display: flex;
		justify-content: space-between;
		width: 370px;
	}
	&__slider-btn {
		color: #c4c4c4;
		cursor: pointer;
		font-weight: 700;
		font-size: 18px;
		padding-top: 35px;
		text-transform: uppercase;
		user-select: none;
		.slider-arrow {
			fill: #c4c4c4;
		}
		&:hover {
			color: $color;
			.slider-arrow {
				fill: $color;
			}
		}
	}
}

.nav {
	position: fixed;
	z-index: 9999;
	width: 100%;
	padding: 50px 0;
	border-bottom-color: $colorDark;
	&--scrolled {
		background: $colorDark;
		border-bottom: 1px solid #565656;
	}
	&__wrap, &__wrap > ul {
		display: flex;
	}
	&__wrap {
		justify-content: space-between;
		align-items: center;
		ul {
			display: none;
		}
	}
	ul, &__tel {
		text-transform: uppercase;
		font-weight: 700;
		font-size: 18px;
	}
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		li {
			padding-right: 30px;
			&:last-child {
				padding-right: 0;
			}
		}
	}
	&__tel {
		position: relative;
		&, &:after, &:before {
			transition: 0.2s all ease;
		}	
		&:hover {
			&:after {
				height: calc(100% + 2px);
			}
			&:before {
				opacity: 1;
			} 
		}
		&:before {
			content: '?';
			position: absolute;
			text-align: center;
			left: 50%;
			transform: translateX(-50%);
			z-index: 2;
			opacity: 0;
		}
		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -2px;
			background: $color;
			width: 100%;
			height: 2px;
			z-index: 1;
		}
	}
	&__burger, &__collapse {
		cursor: pointer;
		svg {
			fill: $colorLight;
		}
		&:hover {
			svg {
				fill: $color;
			}
		}
	}
	&__collapse {
		position: fixed;
		background: $colorDark;
		height: 100%;
		width: 300px;
		top: 0;
		left: -300px;
		bottom: 0;
		z-index: 99;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		transition: 0.3s all ease;
		overflow-y: auto;
		svg {
			position: absolute;
			top: 35px;
			right: 35px;
		}
		ul {
			font-size: 24px;
			li {
				padding-right: 0;
				padding-bottom: 35px;
				&:last-child {
					padding-bottom: 0;
				}
			}
		}
		&-social {
			font-size: 42px;
			margin-top: 80px;
			& > div {
				width: 160px;
				height: 80px;
				display: flex;
				justify-content: space-around;
				align-items: center;
			}
		}
	}
	&__overlay {
		position: fixed;
		top: 0;
		left: -100%;
		width: 100%;
		height: 100%;
		z-index: 9;
	}
}

.teather {
	.row {
		text-align: center;
	}
	&__text {
		font-weight: 700;
		font-size: 16px;
		padding: 15px 0;
		span {
			color: $color;
		}
	}
	&__img {
		width: 100%;
	}
	&__icons-wrap {
		display: flex;
		justify-content: space-around;
	}
}

.rating {
	.container {
		padding-top: 100px;
		padding-bottom: 100px;
	}
	.row {
		align-items: center;
	}
	&__stars-wrap {
		display: flex;
	}
	&__star {
		width: 26px;
		height: 24px;
		margin-right: 10px;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__age {
		font-weight: 700;
		font-size: 50px;
		color: $color;
	}
	&__text {
		font-weight: 700;
		font-size: 24px;
		font-family: $secondFont;
	}
}

.slider {
	&__item {
		position: relative;
		img {
			width: 320px;
			height: 450px;
			object-fit: cover;
		}
	}
	&__box, &__title, &__date, &__hover {
		transition: 0.4s all ease;
		position: absolute;
	}
	&__box {
		background: rgba(51, 51, 51, 0.65);
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		&:hover {
			background: transparent;
			.slider__title, .slider__date {
				opacity: 0;
			}
			.slider__hover {
				transform: translate(-50%, -50%) scale(1);
			}
		}
	}
	&__title {
		top: 40px;
		left: 40px;
		font-size: 24px;
		
	}
	&__title, &__date {
		text-transform: uppercase;
		font-weight: 700;
	}
	&__date {
		font-size: 18px;
		left: 40px;
    	bottom: 40px;
    	transform-origin: left top;
    	transform: rotate(270deg);
	}
	&__hover {
		height: 100px;
		width: 100px;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) scale(0);
		border-radius: 50%;
		font-size: 24px;
		background: $color;
		display: flex;
		justify-content: center;
		align-items: center;
		&:hover {
			height: 120px;
			width: 120px;
		}
	}
	&__empty {
		width: 150px;
		height: 100%;
	}
}


.partners {
	&__wrap {
		display: flex;
		overflow-x: auto;
		text-align: center;
		img {
			margin: 0 80px 80px 0;
			width: 200px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		div:last-of-type {
			img {
				margin-right: 0;
			}
		}
	}
}


.contacts {
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 16px;
		text-align: center;
	}
	&__text {
		padding-bottom: 20px;
	}
	.form {
		display: flex;
		flex-wrap: wrap;
		&__input {
			width: 50%;
			padding-left: 15px;
			&:first-child {
				padding-right: 15px;
				padding-left: 0;
			}
			input {
				height: 35px;
				background: #444444;
				border: 1px solid #616161;
				&:first-child {
					margin-bottom: 30px;
				}
			}
		}
		&__input > input, &__area > textarea {
			background: #444444;
			border: 1px solid #616161;
			width: 100%;
			padding-left: 15px;
			&:hover {
				border-color: $color;
			}
			&::placeholder {
				color: #989898;
			}
		}
		&__input, &__area {
			margin-bottom: 30px;
		}
		&__area {
			height: 100px;
			width: 100%;
			textarea {
				resize: none;
				height: 100%;
				padding-top: 5px;
			}
		}
		&__text {
			font-size: 12px;
			color: #989898;
			font-family: $secondFont;
			order: 2;
		}
		&__text, &__button {
			width: 100%;
			height: 100%;
			text-align: center;
			margin-bottom: 30px;
		}
		&__button {
			order: 1;
			button {
				width: 190px;
			}
		}
	}
}

.footer {
	margin-top: 80px;
	.container {
		border-top: 1px solid #565656;
	}
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		color: #989898;
		font-family: $secondFont;
		font-size: 12px;
		padding: 30px 0;
		text-align: center;
	}
}

.social {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 150px;
	height: 0;
	display: none;
	color: #C4C4C4;
	background-color: $colorDark;
	padding-bottom: 80px;
	overflow: hidden;
	z-index: 999;
	&__icon, &__arrow {
		&:hover {
			color:  $color;
		}
	}
	&.active {
		height: 100%;
	}
	&__title {
		transform: rotate(-90deg);
		font-size: 18px;
		text-transform: uppercase;
		font-weight: 700;
		width: 210px;
		margin-bottom: 100px;
	}
	&__line {
		width: 2px;
		height: 80px;
		background: #C4C4C4;
		margin-top: 20px;
	}
	&__icon {
		margin-top: 20px;
	}
	&__icon, &__arrow {
		font-size: 26px;
	}
	&__arrow {
		position: absolute;
		width: 100%;
		bottom: 0;
		background: $colorDark;
		display: flex;
    	justify-content: center;
		padding: 30px 0;
		cursor: pointer;
		&.active > i {
			transform: rotate(180deg);
		}
	}
	&, &__arrow > i {
		transition: 0.4s all ease;
	}
}