@media (min-width: 576px) {
    h1 {
        font-size: 64px;
    }
    .rating {
        &__stars-wrap {
            margin-bottom: 25px;
        }
        &__star {
            margin-right: 25px;
        }
        &__age {
            font-size: 72px;
        }
    }
    .contacts {
        &__wrap {
            flex-direction: row;
            text-align: end;
        }
    }
    .footer {
        &__wrap {
            flex-direction: row;
        }
    }
}

@media (min-width: 768px) {
    .header {
        height: 100vh;
    }
    .nav {
        &__burger {
            display: none;
        }
        &__wrap {
            flex-direction: column;
            ul {
                display: flex;
            }
        }
    }
    .slider {
        &__item {
            position: relative;
            img {
                width: 442px;
                height: 550px;
            }
        }
    }
    .teather {
        .row {
            text-align: start;
        }
        &__icons-wrap {
            flex-direction: column;
        }
    }
    .rating {
        &__text {
            font-size: 36px;
        }
    }
    .contacts {
        .form {
            &__input {
                width: 25%;
                padding:0 30px 0 0;
                &:first-child {
                    padding:0 30px 0 0;  
                }
            }
            &__area {
                width: 50%;
                height: auto;
            }
            &__text, &__button {
                width: 50%;
            }
            &__text {
                order: 1;
                text-align: start;
            }
            &__button {
                order: 2;
                text-align: end;
            }
        }
    }
    .social {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: column;
    }
}
@media (min-width: 992px) {
    .nav__wrap {
        flex-direction: row;
    }
}
@media (min-width: 1200px) {
    .contacts {
        &__wrap {
            flex-direction: column;
            justify-content: start;
            text-align: start;
        }
    }
}

@media (min-width: 1440px) {
    .social {
        height: 100%;
        &__title {
            display: block;
        }
        &__line {
            display: block;
        }
        &__arrow {
            display: none;
        }
    }
}