.header__slider {
    position: relative;
    width: 350px;
    height: 490px;
    div {
        position: absolute;
        height: 100%;
        width: 100%;
    }
}

.header__slide {
    &.prev {
        top: -20px;
        left: -20px;
        z-index: 999;
        opacity: 0;
        &.down-right {
            transition: 0.3s all ease;
            top: 0;
            left: 0;
            opacity: 1;
        }
    }
    &.active {
        top: 0;
        left: 0;
        z-index: 99;
        &.left-up {
            transition: 0.3s all ease;
            opacity: 0;
            top: -20px;
            left: -20px;
        }
        &.down-right {
            transition: 0.3s all ease;
            top: 20px;
            left: 20px;
            box-shadow:inset 0 0 0 350px rgba(209, 169, 84, 0.65);
        }
    }
    &.passive {
        box-shadow:inset 0 0 0 350px rgba(209, 169, 84, 0.65);
        top: 20px;
        left: 20px;
        z-index: 9;
        &.left-up {
            transition: 0.3s all ease;
            top: 0;
            left: 0;
            box-shadow: none;
        }
        &.down-right {
            transition: 0.3s all ease;
            top: 40px;
            left: 40px;
            opacity: 0;
        }
    }
    &.next {
        opacity: 0;
        box-shadow:inset 0 0 0 350px rgba(209, 169, 84, 0.65);
        top: 40px;
        left: 40px;
        &.left-up {
            transition: 0.3 all ease;
            top: 20px;
            left: 20px;
            opacity: 1;
        }
    }
}